/*
 * Menu Fade In Up
 * ======================== */
.fadeInUp-enter {
  opacity: 0.01;
  -webkit-transform: translate3d(0, 50px, 0);
  transform: translate3d(0, 50px, 0);
}

.fadeInUp-enter.fadeInUp-enter-active {
  opacity: 1;
  
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  transition: all 300ms ease-out;
}

.fadeInUp-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.fadeInUp-leave.fadeInUp-leave-active {
  opacity: 0.01;
  -webkit-transform: translate3d(0, 50px, 0);
  transform: translate3d(0, 50px, 0);
  transition: all 300ms ease-out;
}

/*
 * Menu Fade Left
 * ======================== */

.menu-fadeLeft-enter {
  opacity: 0.01;
}

.menu-fadeLeft-enter.menu-fadeLeft-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-out;
}

.menu-fadeLeft-leave {
  opacity: 1;
  
}

.menu-fadeLeft-leave.menu-fadeLeft-leave-active {
  opacity: 0.01;
  transition: opacity 500ms ease-out;
}

/*
 * SlideIn
 * ======================== */

.slideIn-enter {
  position:absolute;
  //width:100%;
  opacity: 0.01;
  -webkit-transform: translate3d(0, 50%, 1px);
  transform: translate3d(0, 50%, 0);
}

.slideIn-enter.slideIn-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 1px);
  transform: translate3d(0, 0, 0);
  transition: all 500ms ease-out;
}

.slideIn-leave {
  position:absolute;
  //width:100%;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 1px);
  transform: translate3d(0, 0, 0);
  
}

.slideIn-leave.slideIn-leave-active {
  opacity: 0.01;
  -webkit-transform: translate3d(0, -50%, 1px);
  transform: translate3d(0, -50%, 0);
  transition: all 500ms ease-out;
}

/*
 * Appear Bottom
 * ======================== */

.appearFromBottom-enter {
  position:relative;
  opacity: 0.01;
  -webkit-transform: translate3d(0, 100%, 1px);
  transform: translate3d(0, 100%, 0);
}

.appearFromBottom-enter.appearFromBottom-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 1px);
  transform: translate3d(0, 0, 0);
  transition: all 300ms ease-out;
}

.appearFromBottom-leave {
  position:relative;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 1px);
  transform: translate3d(0, 0, 0);
}

.appearFromBottom-leave.appearFromBottom-leave-active {
  opacity: 0.01;
  -webkit-transform: translate3d(0, 100%, 1px);
  transform: translate3d(0, 100%, 0);
  transition: all 300ms ease-out;
}

.fadeInRIGHT{
	animation: fadeInRIGHT 1s ease-in-out;
}

@keyframes fadeInRIGHT{
	from{
		transform: translateX(50%);
		opacity: 0;
	}
	to{
		transform: translateX(0%);
		opacity: 1;
	}
}